import React from 'react';

const ToDoLists = (props) =>{

    
    return(<>
        <div className='todo_style'>
        <i 
        className = "fa fa-items" 
        aria-hidden = "true" 
        onClick={() => {
            props.onSelect(props.id); 
            }} > x  </i> 
        <li> {props.text} </li>
        </div>
        </>
        );
    };

export default ToDoLists;